.button {
  height: 24px;
  color: #d41443;
  fill: #d41443;
  cursor: pointer;
  background-color: #fff;
  border: none;
  border-radius: 20px;
  justify-content: center;
  align-items: center;
  padding: 0 13.6px;
  font-family: DM Sans, sans-serif;
  font-size: 8px;
  line-height: 10px;
  transition: background-color .3s cubic-bezier(.4, 0, .3, 1);
  display: flex;
}

.button:not(.nohover):hover, .button:not(.nohover):focus {
  background-color: #ecebeb;
}

.button--opposite-color {
  color: #fff;
  fill: #fff;
  background-color: #d41443;
  transition: background-color .3s cubic-bezier(.4, 0, .3, 1);
}

.button--opposite-color:hover, .button--opposite-color:focus {
  background-color: #c7133e;
}

.button--open-burger {
  width: 100%;
  height: 44px;
  fill: #d41443;
  gap: 16px;
  padding: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 21px;
}

.button--products {
  width: 40px;
  height: 40px;
  background-image: url("products-arrow.43671bfe.png");
  background-position: center;
  background-size: cover;
  margin: 0 auto;
}

@media screen and (min-device-pixel-ratio: 2), screen and (min-resolution: 192dpi), screen and (min-resolution: 2dppx) {
  .button--products {
    background-image: url("products-arrow@2x.8338e662.png");
  }
}

@media screen and (min-device-pixel-ratio: 3), screen and (min-resolution: 288dpi), screen and (min-resolution: 3dppx) {
  .button--products {
    background-image: url("products-arrow@3x.f36b6bf7.png");
  }
}

